:root {
  --primaryColor: #cf3476 ;
  --HoverColor: #8e3a59 ;
  --frontend: #e5e5e5;
  --cardColor:#f2f3f4 ;
  --whiteColor: hsl(0, 0%, 100%);
  --BlackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --DreamColor: hsl(0, 0%, 93%);
  --grayBg: hsl(0, 0%, 96%);
  --grayText: hsl(190, 190, 190);
  --iputColor: hsl(330, 12%, 97%);
  /* box-shadow: 0 2px 8px 2px rgba(178,178,178,0.45); */
}
.popular {
  padding-top: 15rem;
}
.secContainer .secHeader {
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
}

.textDev .secTitle {
  color: var(--BlackColor);
  line-height: 1.5rem;
  padding-bottom: 0.5rem;
}
.textDev p {
  font-size: 13px;
  color: var(--textColor);
  font-weight: 500;
}
.iconDiv {
  gap: 0.5rem;
}
.iconDiv .icon {
  padding: 0.5rem;
  background-color: var(--textColor);
  border-radius: 50%;
  font-size: 2.5rem;
}
.iconDiv .icon:hover {
  background: var(--BlackColor);
  color: var(--whiteColor);
}

.leftIcon {
  background: var(--BlackColor);
  color: var(--whiteColor);
}
.leftIcon:hover {
  background: var(--grayText);
  color: var(--BlackColor);
}

.mainContent {
  padding: 2rem 0;
  gap: 1rem;
}
.singleDestination {
  background: var(--cardColor);
  overflow: hidden;
  border-radius: 1rem;
  box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.45);
}
.destImage {
  height: 200px;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.destImage img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.overlayInfo {
  position: absolute;
  display: flex;
  align-items: flex-start;
  padding: 0;
  /* padding: 1rem 1.5rem; */
  justify-content: center;
  flex-direction: column;
  background: linear-gradient(rgba(0, 0, 0, 0.546), rgba(0, 0, 0, 0.759));
  bottom: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  opacity: 0;
  transition: .5s ease-out;
}

.overlayInfo:hover {
  overflow: visible;
  padding: 1rem 1.5rem;

  opacity: 1;
}
.overlayInfo h3 {
  color: var(--whiteColor);
  font-weight: 600;
}
.overlayInfo p {
  color: var(--whiteColor);
  opacity: 0.7;
  font-size: 13px;
}
.overlayInfo .icon {
  position: absolute;
  right: 10px;
  bottom: 10px;
  padding: 0.5rem;
  background: var(--primaryColor);
  font-size: 2.5rem;
  border-radius: 50%;
  color: var(--whiteColor);
  border: 1px solid transparent;
  transition: 0.3s ease;
}
.overlayInfo .icon:hover {
  background: var(--HoverColor);
  border: 1px solid var(--primaryColor);
  color: var(--whiteColor);
}
.destFooter {
  padding: 1rem;
}
.destFooter .number {
  font-size: 2rem;
  font-weight: 700;
  color: var(--BlackColor);
  position: relative;
  width: max-content;
}
.destFooter .number::after {
  content: "";
  position: absolute;
  background: var(--primaryColor);
  top: 45%;
  right: -150%;
  height: 5px;
  width: 45px;
  border-radius: 1rem;
}
.destFooter .destText {
  justify-content: space-between;
}
.destText h6 {
  color: var(--BlackColor);
  font-size: 15px;
}
span.flex {
  font-size: 16px;
  font-weight: 600;
  align-items: flex-start;
  transform: translateY(5px);
}
span.flex .icon {
  color: var(--primaryColor);
}

@media screen and (min-width: 480px) {
  .popular {
    padding-top: 10rem;
  }
}
@media screen and (min-width: 556px) {
  .popular .secContainer .secHeader {
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .popular .secContainer .secHeader .textDev {
    text-align: start;
    max-width: 70%;
  }
  .popular .secContainer .secHeader .iconDiv {
    justify-content: flex-end;
  }
}
@media screen and (min-width: 720px) {
  .popular {
    padding-top: 7rem;
  }
}
@media screen and (min-width: 1024px) {
  .popular {
    padding-top: 10rem;
  }
  .secContainer .secHeader .textDev .secTitle {
    font-size: 2.2rem;
  }
  .secContainer .secHeader .textDev p {
    font-size: 15px;
  }
  .popular .secContainer .mainContent {
    grid-template-columns: repeat(4, 1fr);
    gap: 1.5rem !important;
  }
}
