:root {
  --primaryColor: #cf3476 ;
  --HoverColor: #8e3a59 ;
  --frontend: #e5e5e5;
  --cardColor:#f2f3f4 ;
  --whiteColor: hsl(0, 0%, 100%);
  --BlackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --DreamColor: hsl(0, 0%, 93%);
  --grayBg: hsl(0, 0%, 96%);
  --grayText: hsl(190, 190, 190);
  --iputColor: hsl(330, 12%, 97%);
}
.blog .mainContainer {
  gap: 1rem;
}
.mainContainer .singlePost {
  background: var(--cardColor);
  padding: .5rem;
  border-radius: 10px;
  gap: 2rem;
}  
.singlePost .imgDiv {
  height: 200px;
  width: 100%;
  border: 5px solid var(--iputColor);
  border-radius: 1rem;
  box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.406);
  overflow: hidden;
}
.singlePost .imgDiv:hover img {
  transform: scale(1.1);
  transition: 3s ease;
}
.imgDiv img {
  height: 100%;
  object-fit: cover;
}
.singlePost .postDetails h3 {
  color: var(--BlackColor);
  font-weight: 600;
  padding-bottom: 10px;
}
.singlePost .postDetails p {
  color: var(--textColor);
  font-weight: 500;
  font-size: 13px;
}
.singlePost a {
  color: var(--primaryColor);
  font-weight: 600;
  font-size: 15px;
  width: max-content;
  padding-left: .5rem;
  padding-top: 0.5rem;
}
.singlePost a:hover {
  color: var(--HoverColor);
  font-weight: 600;
}
.singlePost a .icon {
  transform: translateX(10px);
}

@media screen and (min-width: 884px) {
  .blog .secContainer .mainContainer {
    grid-template-columns: repeat(2, 1fr);
  }
}
