:root {
  --primaryColor: #cf3476 ;
  --HoverColor: #8e3a59 ;
  --frontend: #e5e5e5;
  --cardColor:#f2f3f4 ;
  --whiteColor: hsl(0, 0%, 100%);
  --BlackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --DreamColor: hsl(0, 0%, 93%);
  --grayBg: hsl(0, 0%, 96%);
  --grayText: hsl(190, 190, 190);
  --iputColor: hsl(330, 12%, 97%);

}
.footer {
  background: var(--cardColor);
  padding: 2rem 0;
}
.footer .secContainer {
  gap: 1.5rem;
  text-align: center;
  height: auto;
}
.footerLogo {
  padding-bottom: 0.5rem;
}
.footerLogo .logo {
  color: var(--BlackColor);
  font-weight: 500;
  cursor: pointer;
  align-items: center;
gap: 10px;
  justify-content: space-around;
}
.logo h1 .icon {
  font-size: 25px;
  color: var(--primaryColor);
  margin-right: 0.5rem;
}
.logoDiv .social {
  gap: 0.5rem;
  margin: auto;
  justify-content: center;
}
.social .icon {
  font-size: 30px;
  background: var(--primaryColor);
  border-radius: 50%;
  padding: 8px;
  color: var(--whiteColor);
}
.social .icon:hover {
  background: var(--HoverColor);
}
.footerLinks {
  align-items: center;
  display: grid;
  height: auto;
}
.footerLinks .linkTitle {
  padding-bottom: 0.5rem;
  color: var(--BlackColor);
  font-weight: 700;
}
.footerLinks li:hover {
  transform: translateX(10px);
}
.footerLinks li a {
  color: var(--textColor);
  font-size: 13px;
  font-weight: 500;
}
.footerLinks .phone,
.email {
  color: var(--textColor);
  font-size: 13px;
  font-weight: 500;
}

@media screen and (min-width: 448px) {
  .footer .secContainer {
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
    align-items: flex-start;
  }
  .footer .secContainer .footerLinks {
    justify-content: center;
  }
}
@media screen and (min-width: 640px) {
  .footer .secContainer {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media screen and (min-width: 900px) {
  .footer .secContainer {
    padding: 2rem;
  }
}
