:root {
  --primaryColor: #cf3476 ;
  --HoverColor: #8e3a59 ;
  --frontend: #e5e5e5;
  --cardColor: #f2f3f4;
  --whiteColor: hsl(0, 0%, 100%);
  --BlackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --DreamColor: hsl(0, 0%, 93%);
  --grayBg: hsl(0, 0%, 96%);
  --grayText: hsl(190, 190, 190);
  --iputColor: hsl(330, 12%, 97%);
}
.secContainer .title {
  color: var(--BlackColor);
  line-height: 1.5rem;
  padding-bottom: 2rem;
  text-align: center;
  font-size: 1.1rem;
}

.secContainer .mainContent {
  gap: 1rem;
}

.mainContent .singleItem {
  background: var(--cardColor);
  padding: 1rem;
  border-radius: 1rem;
  text-align: center;
  box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.45);
}

.singleItem img {
  width: 100px;
  border-radius: 50%;
  padding: 1rem;
  background: rgba(247, 144, 54, 0.265);
}
.singleItem h3 {
  font-weight: 700;
  color: var(--BlackColor);
  padding: 0.5rem;
}
.singleItem p {
  font-size: 13px;
  font-weight: 500;
  color: var(--textColor);
  padding-bottom: 0.5rem;
}

.videoCard {
  margin-top: 2rem;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.73), rgba(0, 0, 0, 0.609)),
    url(../../Assets/img/seeMap.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.cardContent {
  gap: 2rem;
  padding: 2rem 1rem;
}
.cardText h2 {
  color: var(--whiteColor);
  font-weight: 600;
  padding-bottom: 1rem;
}
.cardText p {
  color: var(--whiteColor);
  font-weight: 400;
  opacity: 0.7;
  font-size: 12px;
  padding-bottom: 1rem;
  letter-spacing: 1px;
}
.cardVideo {
  border: 2px solid var(--whiteColor);
  height: 200px;
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
}
.cardVideo video {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

@media screen and (min-width: 399px) and (max-width: 1024px) {
  .about .secContainer .mainContent {
    padding: 0 1rem;
  }
}

@media screen and (min-width: 720px) {
  .videoCard .cardContent {
    gap: 4rem;
    padding: 4rem 3rem;
  }
  .videoCard .cardContent .cardText h2 {
    font-size: 2rem;
  }
  .videoCard .cardContent .cardText p {
    font-size: 15px;
    opacity: 0.7;
  }
  .videoCard .cardContent {
    grid-template-columns: repeat(2, 1fr) !important;
  }
}

@media screen and (min-width: 1024px) {
  .about .secContainer .mainContain {
    padding: 2.5rem 0;
  }
}
