@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  transition: 0.3s ease;
}
:root {
  --primaryColor: #cf3476 ;
  --HoverColor: #8e3a59 ;
  --frontend: #e5e5e5;
  --cardColor:#f2f3f4 ;
  --whiteColor: hsl(0, 0%, 100%);
  --BlackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --DreamColor: hsl(0, 0%, 93%);
  --grayBg: hsl(0, 0%, 96%);
  --grayText: hsl(190, 190, 190);
  --iputColor: hsl(330, 12%, 97%);
  --boxShadow: 0 2px 8px 2px rgba(178, 178, 178, 0.45);
}
/* Default css */
a {
  text-decoration: none;
}
li {
  list-style: none;
}
body{
  background: var(--frontend);
}
.section {
  padding: 4rem 0 2rem;
  background: var(--frontend);
}
.container {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.icon {
  font-size: 2rem;
  cursor: pointer;
}
.flex {
  display: flex;
  align-items: center;
}
.grid {
  display: grid;
  align-items: center;
}
.btn {
  padding: 0.4rem 1.5rem;
  background: var(--primaryColor);
  border: none;
  outline: none;
  border-radius: 3rem;
  cursor: pointer;
}
.btn a {
  color: var(--whiteColor);
  font-weight: 500;
}
.btn:hover a {
  color: var(--whiteColor);
}
.secIntro {
  max-width: 70%;
  text-align: center;
  margin: 0 auto 2rem;
}
.secIntro .secTitle {
  color: var(--BlackColor);
  line-height: 1.5rem;
  padding-bottom: 0.5rem;
}
.secIntro p {
  color: var(--textColor);
  font-size: 13px;
  font-weight: 500;
}
img,
video {
  width: 100%;
  height: auto;
}
input {
  font-size: 100%;
}

/* -------- Media query -------*/
@media screen and (min-width: 556px) {
  .mainContent, .singlePost{
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 884px) {
  .mainContent {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (min-width: 1240px) {
  .container {
    width: 75% !important;
    margin: auto !important;
  }
  .section {
    padding: 5rem 0 3rem;
  }
  .secIntro {
    margin: 0 auto 3rem;
  }
  .secIntro .secTitle {
    line-height: 2rem;
    padding-bottom: 1rem;
    font-size: 2.2rem;
  }
  .secIntro p {
    font-size: 15px;
  }
}
@media screen and (min-width: 2560px) {
  body {
    zoom: 1.7;
  }
}

/* ---- Page Design----- */

::selection {
  background: var(--primaryColor);
  color: var(--whiteColor);
}
::-webkit-scrollbar{
  width: 10px;
  background: var(--grayBg);
}

::-webkit-scrollbar-thumb{
  background: var(--primaryColor);
  border-radius: 10px;
  cursor: pointer;
}