@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  transition: 0.3s ease;
}
:root {
  --primaryColor: #cf3476 ;
  --HoverColor: #8e3a59 ;
  --frontend: #e5e5e5;
  --cardColor:#f2f3f4 ;
  --whiteColor: hsl(0, 0%, 100%);
  --BlackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --DreamColor: hsl(0, 0%, 93%);
  --grayBg: hsl(0, 0%, 96%);
  --grayText: hsl(190, 190, 190);
  --iputColor: hsl(330, 12%, 97%);
  box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.45);
}
.mainContent {
  gap: 1rem;
}
.singleOffer {
  border-radius: 1rem;
  background: var(--cardColor);
  overflow: hidden;
  padding: 0.5rem;
  box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.45);
}
.singleAmenity .icon {
  font-size: 20px;
  color: var(--textColor);
}
.singleAmenity small {
  font-weight: 500;
  color: var(--textColor);
}
.destImage {
  position: relative;
  height: 200px;
  width: 100%;
  border-radius: 1rem;
  overflow: hidden;
}
.destImage img {
  height: 100%;
}
.destImage .discount {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 10px;
  background: var(--primaryColor);
  color: var(--whiteColor);
  border-radius: 3rem;
  font-size: 10px;
  font-weight: 500;
}
.offerbody {
  padding: 1rem;
}
.offerbody .price {
  justify-content: space-between;
}
.price h4 {
  color: var(--BlackColor);
  font-weight: 700;
  font-size: 1.1rem;
}
.price .status {
  padding: 5px 10px;
  color: var(--HoverColor);
  background: var(--grayBg);
  font-size: 10px;
  font-weight: 600;
  border-radius: 3rem;
}
.amenities{
    width: 100%;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 1rem 0;
    gap: .5rem;
}
.amenities .singleAmenity{
    padding: 5px 10px;
    border-radius: 5px;
    background:var(--frontend);
    gap: .5rem;
}
.offerbody .btn{
margin-top: 1rem;
width: 100%;
text-align: center;
justify-content: center;
padding: .6rem;
color: var(--whiteColor);
font-weight: 600;
gap: .5rem;
}
.offerbody .btn .icon{
    color: var(--whiteColor);
}
.offerbody:hover{
    transform: translateY(-7px);
}