@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  transition: 0.3s ease;
}

:root {
  --primaryColor: #cf3476 ;
  --HoverColor: #8e3a59 ;
  --frontend: #e5e5e5;
  --cardColor:#f2f3f4 ;
  --whiteColor: hsl(0, 0%, 100%);
  --BlackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --DreamColor: hsl(0, 0%, 93%);
  --grayBg: hsl(0, 0%, 96%);
  --grayText: hsl(190, 190, 190);
  --iputColor: hsl(330, 12%, 97%);
  --boxShadow: 0 2px 8px 2px rgba(178,178,178,0.45);
}
.home {
  height: 80vh;
  background: linear-gradient(rgba(33, 33, 33, 0.522), rgba(33, 33, 33, 0.522)),
    url("../../Assets/img/home.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.secContainer .homeText {
  text-align: center;
}
.homeText h1.title {
  color: var(--whiteColor);
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.5rem;
}
.homeText .subTitele {
  color: var(--whiteColor);
  font-size: 13px;
  font-weight: 300;
  padding: 1rem 0;
  max-width: 70%;
  text-align: center;
  margin: auto;
}
.homeText .btn {
  padding: 0.6rem 1.5rem;
  background:var(--primaryColor);
}
.homeText .btn:hover{
  background:var(--HoverColor);
}
.homeCard {
  width: 80%;
  border-radius: 1rem;
  padding: 2rem;
  position: absolute;
  gap: 1rem;
  justify-content: center;
  bottom: -7%;
  transform: translate(-50%);
  left: 50%;
  box-shadow: 0 2px 8px 4px rgba(169,169,169, 0.45);
  background: var(--cardColor);

}
.locationDiv,
.distanceDiv,
.pricenDiv {
  width: 100%;
}
.locationDiv label ,
 .distanceDiv label,
  .pricenDiv 
label {
  display: block;
  color: var(--textColor);
  font-weight: 500;
  font-size: 13px;
  padding-bottom: 0.4rem;
}
.locationDiv input ,
 .distanceDiv input,
  .pricenDiv 
input {
  width: 100%;
  background: #eee;
  border-radius: 5px;
  border: none;
  padding: 8px;
  font-size: 13px;
  font-weight: 500;
}
input::placeholder {
  font-size: 13px;
  opacity: 0.5;
  font-weight: 400;
}
input:focus {
  outline: none;
}
.homeCard .btn {
  width: 100%;
  padding: 0.6rem 1.5rem;
  color: var(--whiteColor);
  font-weight: 600;
}
.homeCard .btn:hover{
background: var(--HoverColor);
}

/* -------- Media query -------*/
@media screen and (max-width:480px) {
  .home .secContainer .homeCard{
    bottom: 50px;
    width: 90%;
    grid-template-columns: repeat(2,1fr);
  }
}
@media screen and (min-width:480px) {
.home .secContainer .homeCard{
  bottom: 35px;
  width: 90%;
  grid-template-columns: repeat(2,1fr);
}
.home .secContainer .homeCard .btn{
    transform: translateY(15px);
  }
}

@media screen and (min-width:556px) {
  .home .secContainer .homeCard{
    bottom: 6%;
  }
  .secContainer .homeText .title{
    font-size: 2.5rem;
  }
  .secContainer .homeText .subTitele{
    font-size: 18px;
  }
  }
  @media screen and (min-width:720px) {
    .home .secContainer .homeCard{
      bottom: 11%;
      grid-template-columns: repeat(4,1fr);
    }
    }
    @media screen and (min-width:720px) {
      .secContainer .homeText{
        padding: 1rem 10rem;
      }
      .secContainer .homeText .title{
        font-size: 3.5rem;
        margin: auto;
        line-height: 4rem;
      }
      .secContainer .homeText .subTitele{
     width: 70%;
      }
      }

      @media screen and (min-width:1024px) {
        .secContainer .homeText .subTitele{
          font-size: 20px;
        }
        .home .secContainer .homeCard{
         width: 70%;
        }

        
        }
      
