@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  transition: 0.3s ease;
 
}
:root {
  --primaryColor: #cf3476 ;
  --HoverColor: #8e3a59 ;
  --frontend: #e5e5e5;
  --cardColor:#f2f3f4 ;
  --whiteColor: hsl(0, 0%, 100%);
  --BlackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --DreamColor: hsl(0, 0%, 93%);
  --grayBg: hsl(0, 0%, 96%);
  --grayText: hsl(190, 190, 190);
  --iputColor: hsl(330, 12%, 97%);
  /* box-shadow: 0 2px 8px 2px rgba(178,178,178,0.45); */
}
.navbarSection .header {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1rem;
  z-index: 1000;
}

.logo {
  color: var(--whiteColor);
  font-weight: 500;
  cursor: pointer;
  align-items: center;
  justify-content: center;
}
.logo .icon {
 color: var(--primaryColor);
  font-size: 30px;
  margin-right: 10px;

}
.activeIcon{
  color: var(--frontend);
}
/* -------- Media query -------*/
@media screen and (max-width: 768px) {

  .navBar {
    background: var(--grayBg);
    position: absolute;
    height: max-content;
    padding: 1rem;
    width: 100%;
    top: 0;
    left: -500%;
    box-shadow: 0 2px 4px rgba(209, 209, 209, 0.904);
    transition: 0.5s ease;
    z-index: 1000;
  }
  .navLists {
    flex-direction: column;
    justify-content: center;
    width: 100%;
    margin: auto;
  }

  .navLists .navItem {
    padding: 1rem 0;
  }
  .navItem .navLink {
    color: var(--textColor);
    font-size: 0.9rem;
    font-weight: 600;
  }
  .navLink:hover {
    color: var(--primaryColor);
  }
  .headerBtns{
    flex-direction: column;
  }
  .headerBtns .btn{
    margin-top: 1rem;
  }
  .headerBtns .btn a{
    font-weight: 600;
  }
  .headerBtns .btn a:hover{
    color: var(--whiteColor);
  }
  .loginBtn{
    background: none;
    border: 1px solid var(--primaryColor);
  }
  .loginBtn a{
    color:var(--primaryColor) ;
  }
  .loginBtn:hover{
    background: var(--HoverColor);
  }
  .loginBtn a:hover{
    color: var(--whiteColor);
  }
  .closeNavbar{
    position: absolute;
    top: 1rem;
    right: 1.5rem;
    color: var(--primaryColor);
  }
  .closeNavbar:hover .icon{
    color: var(--HoverColor);
  }
  .activeNavbar{
    left: 0;
  }
}
.toggleNavbar .icon{
font-size: 25px;
color: var(--whiteColor);
}
.toggleNavbar .icon:hover{
  color: var(--primaryColor);
}
.activeHeader{
  background:var(--HoverColor);
  box-shadow: 0 2px 8px 2px rgba(178,178,178,0.45);


}

@media screen and (min-width:767.98px){
  .toggleNavbar , .closeNavbar{
    display: none;
  }
  .header{
    padding: 1.5rem 2rem;
  }
  .navBar .navLists{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
  .navBar .navLists .navItem .navLink{
    color: var(--whiteColor);
    padding:0 .45rem ;
    font-size: .9rem;
    font-weight: 400;
  }
.navLists .navItem .navLink:hover{
color: var(--primaryColor);

}
.headerBtns .btn{
  margin-left: 1rem;
}
.headerBtns .btn a{
  color: var(--whiteColor);
  font-weight: 500;
}

.headerBtns .loginBtn{
  background: none;
  border: 1px solid var(--primaryColor);
}
.headerBtns .loginBtn:hover{
  background: var(--primaryColor);

}
}
@media screen and (min-width:960px){
  .navBar .navLists .navItem{
    padding: 0 .7rem;
  }
}
@media screen and (min-width:1024px){
 .header{
  padding: 1rem 8.5rem !important;
 }
}